* {
  margin: 0;
}

body {
  line-height: 30px;
  background-image: linear-gradient(lightblue, rgb(252, 252, 173));
}

.logo {
  width: 55%;
  margin: 15px;
}

.pokedex-container {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}

.pokemon-container {
  background-color: #e9e9e9;
  padding: 35px;
  margin: 25px;
  display: flex;
  justify-content: space-around;
  flex-basis: 250px;
  flex-grow: 1;
  flex-shrink: 1;
  border-radius: 10px;
  border: 1px solid gray;
  box-shadow: 1px 1px 2px rgba(160, 159, 159, 0.527);
}
